.header{
  background-image: url(../img/header-bg.png);
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  padding-bottom: 487px;
}

.navbar{
  .container.flex-between{
    padding: 41px 10px;
  }
  background-color: #27262a;
}

.nav-logo-link{
  @include flex
  justify-items: center;
  align-items: center;
}

.header-logo-img{
  width: 43px; height: 41px;
  background: url('../img/header-logo.png') -73px -10px
}

.nav-logo-link:hover {
  .header-logo-text {
    color: #ffffff;
  }
  .header-logo-img{
    background: url('../img/header-logo.png') -10px -10px;
  }
}

.header-logo-text{
  font-family: $lato;
  text-transform: uppercase;
  color: #8d81ac;
  font-size: 24px;
  margin-left: 17px;
  font-weight: bold;
}

.hmenu{
  display: flex;
  flex-wrap: wrap;
}

.hmenu-link{
  color: #8d81ac;
  text-transform: capitalize;
  font-weight: bold;
  font-family: $montserat;
  font-size: 16px;
}

.hmenu-item{
  margin-right: 48px;
}

.hmenu-item:nth-last-child(1){
  margin-right: 0;
}

.buy-link{
  background-color: #8d81ac;
  color: #ffffff;
  padding: 11px 35px;
  border-radius: 100px;
}

.header-title {
  color: #ffffff;
  font-size: 60px;
  font-weight: bold;
  font-family: $montserat;
  text-transform: capitalize;
  text-align: center;
  margin-top: 60px;
  margin-bottom: 18px;
}

.header-subtitle{
  color: #8478a1;
  font-size: 22px;
  font-family: $lato;
  text-align: center;
}

.header-sup{
  text-align: center;
  font-family: $montserat;
  color: #50555d;
  font-size: 12px;
  margin-top: 18px;
}

.header-download-btn {
  position: absolute;
  bottom: 0;
  text-align: center;
  left: 50%;
  transform: translateY(50%) translateX(-50%);
  text-transform: capitalize;
  color: #ffffff;
  background-color: #8d81ac;
  padding: 23px 63px;
  border-radius: 100px;
  font-weight: bold;
  font-family: $montserat;
  font-size: 18px;
  border: 1px solid #8d81ac;
  box-sizing: border-box;
}

.header-download-btn:hover{
  background-color: #ffffff;
  color: #8d81ac;
}

.header-btn-subtext {
  position: absolute;
  bottom: -48px;
  text-align: center;
  left: 50%;
  transform: translateX(-50%) translateY(50%);
  text-transform: capitalize;
  color: #a5acb3;
  font-size: 12px;
  font-family: $montserat;
}

